<template>
  <div id="app">
    <div class="reveal">
      <div class="slides">
        <section data-transition="zoom" v-for="chart in charts" :key="chart.type">
          <div class="slide-title-blue">
            <div class="item">{{ chart.display }}</div>
            <div class="item">
              <font class="lineUp" :color="chart.color">{{ chart.price }}&nbsp;<font size="16"><i>{{ chart.change }}</i>
                </font>
              </font>
            </div>
            <div class="item">
              <span v-if="chart.type == 1"
                style="color: rgb(54, 98, 130);position:absolute; font-size: 25px; top:15px;">日线</span>
              <span v-else style="color: rgb(54, 98, 130);position:absolute; font-size: 25px; top:15px;">周线</span>
              <span style="color: rgb(54, 98, 130); font-size: medium;">{{ chart.date }}</span>
            </div>
          </div>
          <div class="flex-container" :style="getBGColor(chart.type)">
            <img :src="require(`./assets/zmcj-chart/${chart.img}`)" style="flex: 1" />
          </div>
        </section>
      </div>
    </div>
    <div>
      <audio ref="audio">
        <source :src="file" />
      </audio>
    </div>
  </div>
</template>

<script>
import Reveal from "reveal.js";
//import Markdown from "reveal.js/plugin/markdown/markdown.js";
//import Highlight from "reveal.js/plugin/highlight/highlight.js";
import "reveal.js/dist/reveal.css";
import "reveal.js/dist/theme/night.css";
import "reveal.js/plugin/highlight/monokai.css";
import chartData from "./assets/charts.json";
const HCOIN = 1
export default {
  name: "App",
  data() {
    return {
      charts: chartData,
      file: "https://www.chosic.com/wp-content/uploads/2020/07/alexander-nakarada-superepic.mp3",
    };
  },
  mounted() {
    console.log(this.charts)
    Reveal.initialize({
      //autoSlide: 4000,
      controls: true,
      progress: false,
      center: true,
      hash: true,
      transition: "none",
      width: 1000,
      height: 800,
      // Factor of the display size that should remain empty around
      // the content
      margin: 0.0,
      // Bounds for smallest/largest possible scale to apply to content
      //minScale: 0.2,
      //maxScale: 2.0,
      // Learn about plugins: https://revealjs.com/plugins/
      plugins: [],
        //RevealZoom,
        //RevealNotes,
        //RevealSearch,
        //RevealMarkdown,
        //RevealHighlight,
      
    });
    Reveal.addKeyBinding({ keyCode: 83, key: 'S', description: 'Start Sliding' }, () => {
      Reveal.destroy();
      this.$refs.audio.play();
      //setTimeout(() => {
      Reveal.initialize({
        autoSlide: 4000,
        loop: false
      })
      //}, 2000);
    })
  },
  methods: {
    getBGColor(type) {
      switch (type) {
        case HCOIN:
          return "background-color: #272525cb"
      }
      return 'background-color: #ffffff;'
    }
  }
};
</script>

